@import url("https://fonts.googleapis.com/css?family=Scope+One");
/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
.Home {
  box-sizing: border-box;
  position: absolute;
  display: flex;
  height: calc(100vh - 100px);
  margin: 1rem auto;
  flex-direction: column;
  justify-content: center; }
  .Home--heading {
    font-size: 6rem;
    font-weight: 400;
    margin: 1rem 0; }
  .Home--subHeading {
    font-weight: 400;
    margin: 1rem 0; }
  .Home--icon {
    padding: 0.5rem;
    font-size: 2.2rem;
    color: #111;
    transition: all 0.4s ease; }
    .Home--icon:hover {
      color: orangered; }
  .Home--text-secondaryColor {
    color: orangered; }
