@import url("https://fonts.googleapis.com/css?family=Scope+One");
/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
.ContactMe {
  margin-bottom: 5rem; }
  .ContactMe--heading {
    font-size: 6rem;
    font-weight: 400;
    margin: 1rem 0; }
  .ContactMe--subHeading {
    font-weight: 400;
    margin: 1rem 0; }
  .ContactMe--text-secondaryColor {
    color: orangered; }
  .ContactMe--main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 5rem; }
  .ContactMe--contact {
    border: 0.3rem white solid;
    margin: 1rem;
    padding: 0.75rem 1.5rem;
    transition: all 0.4s ease; }
    .ContactMe--contact:hover {
      padding: 0.5rem 1.2rem;
      background: orangered; }
      .ContactMe--contact:hover .ContactMe--text-secondaryColor {
        color: #eee; }
