@import url("https://fonts.googleapis.com/css?family=Scope+One");
/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
.TechIcon {
  box-sizing: border-box;
  width: 70px;
  margin: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }
  .TechIcon--icon {
    display: block;
    width: 70px;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.4s ease; }
    .TechIcon--icon:hover {
      transform: scale(1.1); }
  .TechIcon--subtitle {
    display: block;
    margin-top: 0.5rem;
    text-align: center;
    color: #111; }
